<template>
  <div v-if="open" class="mask" @click="$emit('click', false)"></div>
</template>

<script>
export default {
  model: {
    prop: 'open',
    event: 'click'
  },
  props: {
    open: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style>
.mask{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.6);
  z-index: 100;
}
</style>



